body.is-cont {
	.toparea {
		position: relative;
		padding-top: 140px;
		padding-bottom: 130px;
		color: #fff;
		background-repeat: no-repeat;
		background-position: 67% 100%;
		background-color: #001484; }
	.toparea__in {
		position: relative; }
	.toparea__svg {
		right: 150px;
		bottom: -380px; } }

// content
.content {
	padding-bottom: 65px;
	font-size: 1.8rem;
	h2,h3,h4 {
		line-height: 1.2; }
	h1, h2, h3, h4, p, pre {
		&:first-child {
			margin-top: 0; } }
	h2 {
		margin: 90px 0 20px;
		@include f(el, 50); }
	h3 {
		margin: 65px 0 20px;
		@include f(el, 35); }
	h4 {
		margin: 40px 0 20px;
		@include f(m, 18); }
	.title {
		font-size: 5rem;
		&_small {
		  @include f(el, 35); } }
	p {
		margin-top: 20px; }
	a {
		color: inherit;
		line-height: 1.1;
		border-color: rgba(#000, .2);
		display: inline-block;
		&:hover {
			color: #FF5858; } }
	ul,
	ol {
		padding-right: 25px;
		@include r(768) {
			margin-left: 18px;
			padding-right: 0; }
		li {
			margin-top: 18px;
			&:before {
				position: absolute; }
			&:first-child {
				margin-top: 0; } } }
	ul {
		li {
			position: relative;
			&:before {
				content: "";
				top: 10px;
				left: -15px;
				width: 4px;
				height: 4px;
				border-radius: 4px;
				background: #003CFF; } } }
	ol {
		counter-reset: list;
		li {
			position: relative;
			&:before {
				top: 0;
				left: -18px;
				content: counter(list);
				counter-increment: list;
				color: #003cff; } } }
	table {
		min-width: 900px;
		thead {
			tr {
				&:hover {
					color: inherit;
					background: inherit; } } }
		tr {
			transition: background .2s, color .2s;
			&:hover {
				color: #fff;
				background: #003CFF;
				td {
					&:first-child,
					&:last-child {
						&:before {
							background: #003CFF; } } } } }
		th,
		td {
			&:first-child {
				@include r(900) {
					padding-left: 5px; } }
			&:nth-child(4),
			&:nth-child(5) {
				text-align: right; } }
		th {
			position: relative;
			padding-right: 9px;
			text-align: left;
			color: #999;
			font-size: 1.5rem;
			@include f(el);
			&:last-child {
				&:after {
					position: absolute;
					content: "";
					bottom: 0;
					left: 100%;
					width: 30vw;
					right: 0;
					opacity: 0.5;
					border-bottom: 1px solid; } }
			&:before {
				position: absolute;
				content: "";
				bottom: 0;
				left: 0;
				right: 0;
				opacity: 0.5;
				border-bottom: 1px solid; }
			a {
				color: #467aff;
				border-bottom: none; } }
		td {
			position: relative;
			padding: 9px;
			padding-left: 0;
			vertical-align: top;
			&:first-child,
			&:last-child {
				&:before {
					position: absolute;
					content: "";
					top: 0;
					bottom: 0;
					width: 40vw;
					transition: background .2s;
					background: transparent;
					@include r(900) {
						display: none; } } }
			&:first-child {
				&:before {
					right: 100%; } }
			&:last-child {
				&:before {
					left: 100%; } }
			&.is-w21 {
				width: 21%; }
			&.is-w24 {
				width: 24%; }
			&.is-w15 {
				width: 15%; }
			&.is-w16 {
				width: 16%; }
			&.is-w55 {
				width: 55%;
				@include r(900) {
					width: 0; } } }
		strong {
			@include f(m); } }
	.btn,
	a.btn {
		display: inline-block;
		box-sizing: border-box;
		height: 45px;
		height: 4.5rem;
		min-width: 140px;
		min-width: 14rem;
		padding: 0 20px;
		padding: 0 2rem;
		line-height: 43px;
		line-height: 4.3rem;
		border: 1px dotted;
		border-radius: 3px;
		border-radius: 0.3rem;
		background: none;
		@include f(el, 16);
		color: $color-blue;
		@include trans;
		transition-property: color, background;
		text-decoration: none;
		text-align: center;
		cursor: pointer;
		&:hover {
			color: red; }
		&_fluid {
			display: block;
			width: 100%; }
		&_solid {
			background: $color-blue;
			border: none;
			color: white;
			height: 50px;
			height: 5rem;
			line-height: 50px;
			line-height: 5rem;
			@include f(eb, 14, 150);
			text-transform: uppercase;
			&:hover {
				color: white;
				background: #002fc8; } }
		&_big {
			height: 50px;
			height: 5rem;
			line-height: 50px;
			line-height: 5rem; }
		&_long {
			padding-left: 40px;
			padding-left: 4rem;
			padding-right: 40px;
			padding-right: 4rem; }
		&_bord {
			border-style: solid; } }
	.field__label {
		letter-spacing: initial;
		font-size: 1.8rem;
		line-height: 1; }
	textarea.input {
		line-height: 1.4;
		@include f(m); } }

.blue {
	table {
		th {
			color: rgba(#fff, .5); } } }

// line
hr {
	margin: 35px -30vw 35px 0;
	padding: 0;
	height: 1px;
	border: none;
	background: #E7E7E7;
	& + .inf {
		margin-top: -25px; } }

// table
.table {
	@include r(900) {
		overflow-x: auto; } }


// center
.center {
	width: 86%;
	@include r(1200) {
		width: 100%; } }

// pre
.pre {
	position: relative;
	margin: 30px -38px;
	padding: 25px 36px;
	background-color: #f0f1f3;
	border-radius: 2px;
	pre {
		-ms-overflow-x: auto;
		overflow-x: auto; }
	@include r(1200) {
		margin-left: 0;
		margin-right: 0; }
	.code-copy {
		position: absolute;
		top: 11px;
		right: 12px;
		color: #787879;
		line-height: 1.1;
		transition: opacity .3s;
		border-bottom: 1px dotted #969698;
		display: inline-block;
		@include f(r, 13); } }

// series
.series {
	margin: 38px 0 0 -15px;
	padding-left: 15px;
	padding-bottom: 1px;
	overflow: hidden;
	@include clr;
	&:first-child {
		margin-top: 0; }
	&.is-mod {
		margin-top: 8px;
		margin-bottom: -3rem;
		@include r(768) {
			margin-bottom: 0; } }
	&__in {
		margin-top: -3rem; }
	[class^="col"] {
		float: left; }
	.col-right {
		float: right; }
	.col-50 {
		@include r(768) {
			margin-top: 40px;
			width: 100%;
			float: none;
			&:first-child {
				margin-top: 0; } } }
	.col-26 {
		margin-top: 3rem;
		float: none;
		padding-right: 10px;
		min-width: 190px;
		vertical-align: top;
		box-sizing: border-box;
		display: inline-block; } }

// images
.images {
	margin: -3% -3% 0 0;
	@include clr;
	[class^="col"] {
		margin: 3% 3% 0 0;
		padding: 0;
		width: calc(30.3%);
		box-sizing: border-box;
		@include r(768) {
			width: 100%;
			margin: 3rem 0 0;
			&:first-child {
				margin-top: 0; } } }
	img {
		width: 100%;
		display: block; } }

// accord
.accord {
	margin-top: 40px;
	&:hover {
		.accord__top {
			color: red;
			svg {
				path {
					stroke: red; } } } }
	& + .accord {
		margin-top: 20px; }
	&:first-child {
		margin-top: 0; }
	&.is-active {
		.accord__top {
			color: #000;
			svg {
				path {
					stroke: #000; } }
			.icon {
				@include rotate(-180); } } }
	&__top {
		position: relative;
		@include f(m);
		cursor: pointer;
		user-select: none;
		color: #003cff;
		transition: color .3s;
		.icon {
			position: absolute;
			left: -15px;
			top: 10px;
			line-height: 1;
			font-size: 0;
			transition: transform .3s; }
		svg {
			display: block;
			path {
				stroke: #003cff;
				transition: stroke .3s; } }
		span {
			border-bottom: 1px dotted; } }
	&__block {
		padding-top: 20px;
		display: none; } }

// warn
.warn {
	margin-top: 20px;
	position: relative;
	@include r(768) {
		margin-left: 35px; }
	&:first-child {
		margin-top: 0; }
	.icon {
		position: absolute;
		content: "";
		top: 0;
		left: -35px; }
	strong {
		@include f(m); }
	p {
		margin-top: 0; } }


// inf
.inf {
	margin-top: 8px;
	font-size: 1.5rem;
	color: #888;
	&:first-child {
		margin-top: 0; }
	h5 {
		color: #000;
		font-size: 1.5rem;
		@include f(l); }
	p {
		margin-top: 12px;
		&:first-child {
			margin-top: 0; } } }

// sort
.sort {
	position: relative;
	&:hover {
		svg {
			path {
				stroke: #FF5858; } } }
	&.is-active {
		.icon {
			@include rotate(180); } }
	.icon {
		position: absolute;
		top: 5px;
		right: -15px;
		line-height: 1;
		font-size: 0;
		transition: transform .3s;
		svg {
			path {
				transition: stroke .3s; } } } }

// field
.file {
	width: 100%;
	font-size: 18px;
	@include clr;
	&:hover {
		.file__name {
			border-color: rgba(#FF5858, 0.5); } }
	svg,
	img,
	.ico {
		float: left;
		margin: 2px 10px 0 -8px;
		display: block; }
	&__heft {
		margin-top: 4px;
		font-size: 15px;
		color: #888;
		display: block; }
	&__name {
		transition: border-color .3s;
		border-bottom: 1px dotted rgba(#000, .5);
		display: inline-block; } }

// full-img
.full-img {
	padding-bottom: 38.4%; }

// img
.img {
	min-width: 100%; }

// label
.label {
	margin-bottom: 15px;
	color: #9D9CA7;
	display: block;
	&:last-child {
		margin-bottom: 0; } }

// checkbox
.checkbox {
	span {
		&:before {
			border: 1px solid #003cff;
			border-radius: 2px; }
		&:after {
			top: 0;
			left: 4px;
			width: 14px;
			height: 15px;
			background: url(../img/svg/check-arr.svg) no-repeat 0 0;
			will-change: opacity;
			opacity: 0; } }
	input:checked {
		& + span {
			&:after {
				opacity: 1; } } } }

// radiobox
.radiobox {
	span {
		&:before {
			border-radius: 50%;
			border: 1px solid #003cff; }
		&:after {
			top: 7px;
			left: 6px;
			width: 6px;
			height: 6px;
			border-radius: 50%;
			background: #FFF; } } }

.checkbox,
.radiobox {
	position: relative;
	margin-bottom: 18px;
	padding-left: 25px;
	line-height: 1.2;
	width: 100%;
	cursor: pointer;
	user-select: none;
	display: inline-block;
	&:last-child {
		margin-top: 0; }
	span:before,
	input {
		position: absolute;
		content: "";
		top: 1px;
		left: 0;
		width: 16px;
		height: 16px;
		transition: {} }
	span:after {
		position: absolute;
		content: ""; }
	input {
		opacity: 0;
		z-index: 2;
		cursor: pointer;
		&:checked {
			& + span {
				&:before {
					background: #003cff; } } } } }
.alert {
	padding: 16px;
	text-align: center;
	border-radius: 2px;
	background: #AEFFC9;
	&.is-error {
		background: #FFCAC3; } }
