// icons png
.ico {
	display: inline-block;
	&-pdf {
		@include retina-sprite($ico-pdf-group); }
	&-xls {
		@include retina-sprite($ico-xls-group); }
	&-zip {
		@include retina-sprite($ico-zip-group); }
	&-doc {
		@include retina-sprite($ico-doc-group); }
	&-ppt {
		@include retina-sprite($ico-ppt-group); }
	&-rar {
		@include retina-sprite($ico-rar-group); }
	&-png {
		@include retina-sprite($ico-png-group); }
	&-tif {
		@include retina-sprite($ico-tif-group); }
	&-gif {
		@include retina-sprite($ico-gif-group); }
	&-jpg {
		@include retina-sprite($ico-jpg-group); }
	&-unknown {
		@include retina-sprite($ico-unknown-group); } }
