.l {
  padding: 0 percentage(170/1200) 0 percentage(170/1200);
  & + .full-img {
    margin-top: -1rem; }
  @include r(1400) {
    padding: 0 percentage(150/1200) 0 percentage(150/1200); }
  @include lg {
    padding-left: 40px;
    padding-right: 40px; }
  @include sm {
    padding-left: 15px;
    padding-right: 15px; } }

.row {
  margin: 0 -30px;
  @include clr; }

.col {
  float: left;
  box-sizing: border-box;
  padding: 0 30px;
  width: 100%; }

.col-right {
  float: right;
  padding-right: 10px;
  width: 33.6%;
  box-sizing: border-box;
  @include r(768) {
    margin-top: 20px;
    padding-right: 0;
    width: 100%;
    &:first-child {
      margin-top: 0; } } }

.col-left {
  float: left;
  padding-right: 60px;
  width: 66.4%;
  box-sizing: border-box;
  @include r(768) {
    padding-right: 0;
    width: 100%; } }

$col-sizes: 80, 75, 60, 50, 26, 25, 20;
@each $size in $col-sizes {
  .col-#{$size} {
    width: #{$size}%;
    &.is-mob {
      @include r(768) {
        margin-top: 20px;
        width: 100%;
        &:first-child {
          margin-top: 0; } } } } }

.col-66 {
  width: 66.66%; }

.col-33 {
  width: 33.33%; }
